.menu-container {
  background-color: #fff8e1;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}

.selected-item {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 32vw;
  display: flex;
}

.item-img {
  justify-content: center;
  align-items: center;
  width: 90vw;
  height: 32vw;
  padding: 4em;
  display: flex;
}

.item-details {
  flex-direction: column;
  justify-content: start;
  align-items: start;
  width: 100%;
  height: 100%;
  padding: 2.5rem;
  display: flex;
}

.item-img img {
  background-position: center;
  border-radius: 15px;
  width: 90%;
  height: 110%;
}

.item-heading h1 {
  font-family: Gilroy, Gilroy Bold;
  font-size: 3.5vw;
}

.item-description {
  margin-top: 1vw;
  margin-left: 1vw;
  font-size: 1vw;
}

.item-cuisines {
  color: #ffa726;
  margin-bottom: 1vw;
}

.item-rating {
  align-items: center;
  margin-bottom: .3vw;
  font-family: Gilroy, Gilroy Bold;
  display: flex;
}

.item-rating img {
  width: 1vw;
  height: 1vw;
  margin-right: .35vw;
}

.item-address {
  justify-content: start;
  align-items: center;
  margin-top: .3vw;
  font-size: 1.2vw;
  display: flex;
}

.item-address h5 {
  color: #676a6d;
  margin-left: 1.3vw;
}

.item-delivery-time {
  margin-top: .3vw;
  font-size: 1.2vw;
}

.item-delivery-fee {
  color: #676a6d;
  align-items: center;
  margin-top: 1vw;
  display: flex;
}

.item-delivery-fee img {
  width: 1.5vw;
  height: 1.5vw;
  margin-right: .5vw;
}

.menu-list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
  display: flex;
}

.menu-list h1 {
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: Gilroy, Gilroy ExtraBold;
  font-size: 50px;
}

.break-line {
  background-color: #7c7c7dc3;
  border-radius: 20px;
  width: 95%;
  height: 1.5px;
  margin: 20px;
}
